import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { availablePups, Pup } from '@/lib/puppyData';
import { motion } from 'framer-motion';
import { ArrowRight, Dog, PawPrint } from 'lucide-react';

// A small helper function to format the price nicely
const formatPrice = (price: number) => {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0,
  }).format(price);
};

// --- Premium Puppy Card Component ---
// We create a separate component for the card to keep our code clean.
const PupCard = ({ pup, index }: { pup: Pup; index: number }) => {
  // Animation variants for the staggered fade-in effect
  const cardVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        delay: index * 0.1, // Each card will appear slightly after the previous one
        duration: 0.5,
        ease: 'easeOut',
      },
    },
  };

  return (
    <motion.div variants={cardVariants}>
      <Link href={`/puppies/${pup.slug}`} className="block group">
        <div className="bg-white rounded-xl shadow-md hover:shadow-2xl overflow-hidden transition-all duration-300 hover:-translate-y-2 border border-slate-100">
          <div className="relative overflow-hidden h-72">
            <Image
              src={pup.image}
              alt={pup.name}
              fill
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
              className="object-cover transition-transform duration-500 ease-in-out group-hover:scale-105"
            />
            {/* Status Badge */}
            <div className="absolute top-4 left-4 bg-primary text-white text-xs font-bold px-3 py-1.5 rounded-full shadow-lg" style={{ backgroundColor: 'rgb(var(--primary))' }}>
              AVAILABLE
            </div>
            {/* Price Badge */}
            <div className="absolute top-4 right-4 text-white text-sm font-bold px-4 py-1.5 rounded-full shadow-lg backdrop-blur-sm bg-black/30">
              {formatPrice(pup.price)}
            </div>
          </div>
          <div className="p-6 text-left"> {/* Text is now left-aligned */}
            <h3 className="text-2xl font-bold mb-2" style={{ color: 'rgb(var(--primary))' }}>
              {pup.name}
            </h3>
            {/* Details with icons */}
            <div className="flex items-center space-x-4 text-slate-600 mb-4 text-sm">
              <span className="flex items-center">
                <Dog className="w-4 h-4 mr-1.5 text-slate-400" /> {pup.gender}
              </span>
              <span className="flex items-center">
                <PawPrint className="w-4 h-4 mr-1.5 text-slate-400" /> {pup.breedType}
              </span>
            </div>
            {/* Description with a fixed height to keep cards aligned */}
            <p className="text-slate-500 mb-6 h-12 line-clamp-2">{pup.description}</p>
            <div className="font-bold inline-flex items-center transition-colors" style={{ color: 'rgb(var(--secondary))' }}>
              View Details
              <ArrowRight className="w-4 h-4 ml-2 transition-transform duration-300 group-hover:translate-x-1" />
            </div>
          </div>
        </div>
      </Link>
    </motion.div>
  );
};

// --- Main Puppy Preview Section ---
export const PuppyPreview = () => {
  // IMPORTANT: We first filter for 'Available' pups, then take the first 3.
  const pupsToShow = availablePups
    .filter((pup) => pup.status === 'Available')
    .slice(0, 3);

  // Animation container to orchestrate the card animations
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
      },
    },
  };

  return (
    <section className="py-24 bg-slate-50"> {/* Softer background color */}
      <div className="container mx-auto px-6">
        <div className="text-center mb-16 max-w-3xl mx-auto">
          <h2 className="text-4xl lg:text-5xl font-bold mb-4" style={{ color: 'rgb(var(--secondary))' }}>
            Meet Our Newest Pups
          </h2>
          <p className="text-lg text-slate-600">
            Welcome the newest members of the family. Each puppy is raised with love, care, and the highest standards of breeding.
          </p>
        </div>
        
        {pupsToShow.length > 0 ? (
          <motion.div
            className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8"
            variants={containerVariants}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.2 }}
          >
            {pupsToShow.map((pup, index) => (
              <PupCard key={pup.slug} pup={pup} index={index} />
            ))}
          </motion.div>
        ) : (
          <div className="text-center text-xl text-slate-600 bg-white border border-slate-200 p-16 rounded-xl">
            <p className="font-bold mb-2">No Puppies Available Right Now</p>
            <p>Our upcoming litters are on the way. Please check back soon or join our waiting list!</p>
          </div>
        )}

        <div className="text-center mt-16">
          <Link 
            href="/puppies" 
            className="inline-flex items-center group text-white font-bold py-4 px-10 rounded-lg transition-all duration-300 shadow-lg hover:shadow-xl hover:-translate-y-1" 
            style={{ backgroundColor: 'rgb(var(--primary))' }}
          >
            View All Available Puppies
            <ArrowRight className="w-5 h-5 ml-2 transition-transform duration-300 group-hover:translate-x-1.5" />
          </Link>
        </div>
      </div>
    </section>
  );
};